import {
  FormScroll,
  FullScreenWrapper,
  MyKeyboardAvoidingView,
  MySelectorInput,
  MyTextInput,
  MyUserPickerInput,
  ScreenWrapper, UIInput
} from "@true/ui";
import {memo, useCallback} from 'react';
import {Fonts, styled} from '@true/core';
import Animated, {runOnUI, useSharedValue} from 'react-native-reanimated';
import {AnimatedBottomSpace} from '@/components/AnimatedBottomSpace';
import {
  ETypeChannel,
  ICreateChannel,
  initCreateChannel,
} from '@/store/channels/type';
import {HeaderCreatePost} from '@/screens/CreatePostScreen/HeaderCreatePost';
import {ETypeUpdate} from '@/store/updates/type';

export const CreateChannelScreen = memo(function () {
  const params = useSharedValue<ICreateChannel>(initCreateChannel);

  const typeChannelOptions = [
    {
      label: 'Public, Company-wide',
      value: ETypeChannel.COMPANY,
    },
    {
      label: 'Team or department',
      value: ETypeChannel.TEAM,
    },
  ];
  const onChangeValue = useCallback(
    (key: string, value: any) => {
      runOnUI((_key: string, _value) => {
        'worklet';

        params.value = {
          ...params.value,
          [_key]: _value,
        };
      })(key, value);
    },
    [params],
  );

  return (
    <FullScreenWrapper>
      <MyKeyboardAvoidingView>
        <HeaderCreatePost
          loading={false}
          onPress={() => {
            console.log('aaa');
          }}
          isActive={false}
          type={ETypeUpdate.CORE_VALUE}
        />
        <SContainer>
            <SWrapperInput>
              <UIInput.Text
                title={'Channel name'}
                keyName={'name'}
                params={params}
                placeholder={'---'}
                onChangeValue={onChangeValue}
                required
              />
            </SWrapperInput>
            <SWrapperInput>
              <UIInput.Text
                title={'Channel path'}
                keyName={'path'}
                params={params}
                placeholder={'---'}
                onChangeValue={onChangeValue}
                required={true}
              />
            </SWrapperInput>
            <SWrapperInput>
              <UIInput.Selector
                title={'Channel type'}
                keyName={'metatype'}
                placeholder={'---'}
                onChangeValue={onChangeValue}
                options={typeChannelOptions}
                required
              />
            </SWrapperInput>
            <SWrapperInput>
              <UIInput.UserPicker
                title={'Channel admins'}
                type={'user'}
                keyName={'owners'}
                onChangeValue={onChangeValue}
                multiple
              />
            </SWrapperInput>
            <SWrapperInput>
              <UIInput.UserPicker
                title={'Channel members'}
                type={'user'}
                params={params}
                keyName={'followers'}
                onChangeValue={onChangeValue}
                multiple
              />
            </SWrapperInput>
            <SWrapperInput>
              <UIInput.Text
                title={'Short description'}
                keyName={'content'}
                placeholder={'---'}
                params={params}
                onChangeValue={onChangeValue}
                required
              />
            </SWrapperInput>
            <AnimatedBottomSpace />
        </SContainer>
      </MyKeyboardAvoidingView>
    </FullScreenWrapper>
  );
});
const SContainer = styled(Animated.View)`
  flex: 1;
`;
const SWrapperInput = styled.View`
  margin: 8px 16px 0 16px;
`;
